html, body, #root {
  width: 100%;
  display: flex;
  background: #1E1E1E;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Pathway Gothic One", sans-serif;
  letter-spacing: 0.5px;
  word-spacing: 4px;
  font-size: 25px;
  color: white;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.video-responsive {
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
  width: 100%;
  margin-top: 10px;
}

.video-responsive iframe {
  left: 0px;
  top: 0px;
  height: 100%;
  width: 100%;
  position: absolute;
}
